.App {
  min-height: 100vh;
}

.Home {
  min-height: 100vh;
}
.heading {
  text-transform: capitalize;
  width: 100%;
  margin: 20px;
}
.CollegesList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.College {
  flex-basis: 46%;
  min-height: 375px;
  margin: 20px;
}
.college-image-wrapper {
  width: 100%;
  position: relative;
}
.college-image-wrapper img {
  width: 100%;
  height: 100%;
}

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.overlay {
  position: absolute;
  background-color: black;
  opacity: 0.7;
  width: 100%;
  height: 98%;
  z-index: 10;
  top: 0;
}

.rating-wrapper {
  background-color: #f5a626;
  position: absolute;
  top: 3.5vh;
  left: 80%;
  z-index: 10;
  padding: 0px 10px;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.rating {
  display: flex;
  align-items: center;
  justify-content: center;
}
.rating-number {
  font-weight: 500;
  font-size: 1.2rem;
}

.rating-max {
  font-weight: 400;
  font-size: 1rem;
}

.college-tags-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  z-index: 10;
  position: absolute;
  top: 75%;
}

.college-tags-left {
  display: flex;
  justify-content: space-between;
}
.college-tags-item {
  background-color: white;
  margin: 0 10px;
  font-size: 0.7rem;
  padding: 2px 5px;
  border-radius: 50px;
  color: rgba(0, 0, 0, 0.664);
  display: flex;
  justify-content: center;
  /* width: auto; */
}

.college-tags-right {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.849);
  width: 40%;
  font-weight: 500;
}

/* College Details */
.college-details-wrapper {
  display: flex;
  width: 100%;
}

.college-details-left {
  min-width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.college-details-left div {
  padding: 3px;
}
.college-details-left-top {
  display: flex;
  align-items: center;
}
.college-title {
  font-size: 1.2rem;
  font-weight: 500;
}
.college-star-rating {
  display: flex;
  padding: 5px;
}
.college-nearest-details {
  font-size: 0.9rem;
  color: #444444;
}
.college-nearest-famous {
  display: flex;
  align-items: center;
}
.match-details {
  font-size: 0.9rem;
}
.famouse-near-details {
  color: #444444;
  font-size: 0.8rem;
}
.match-details {
  color: #37b396;
  width: auto;
}
.offer-wrapper {
  background-color: #ccf5e7;
  border-radius: 0px 30px 30px 0px;
  font-size: 0.9rem;
  padding-left: 20px !important;
}
.offer-wrapper a {
  text-decoration: none;
}
.bold {
  font-weight: 500;
}
.down-opacity {
  opacity: 0.7;
}
.num-color {
  color: #37b396;
  font-weight: 600;
}
/* details right */
.college-details-right {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 5px;
  justify-content: flex-start;
}
.discount {
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
}
.discount p {
  text-decoration: line-through;
  padding: 2px;
}

.fees-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-end;
}
.fees {
  font-size: 1.4rem;
  font-weight: 500;
  color: red;
}
.fees-per-semester {
  font-size: 0.6rem;
  width: 100%;
  text-align: right;
}
.amenities-wrapper {
  color: #37b396;
  display: flex;
  font-size: 0.8rem;
  align-items: center;
}
.amentity {
  padding: 5px;
}

.loading {
  display: flex;
  justify-content: center;
  flex-basis: 100%;
  flex: 1 1 auto;
  width: 100%;
}
.loading img {
  width: 5%;
}

.discount-tag {
  position: relative;
  width: 25px;
  height: 15px;
  margin-left: 15px;
  margin-right: 15px;
  background-color: red;
  text-align: center;
  /* line-height: 40px; */
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-weight: 600;
}

.discount-tag:before {
  content: "";
  position: absolute;
  right: 100%;

  border-top: 7px solid transparent;
  border-right: 10px solid red;
  border-bottom: 8px solid transparent;
}

.dot {
  width: 3px;
  height: 3px;
  border-radius: 100%;
  background-color: white;
}

.promoted-ribbon {
  position: absolute;
  top: -30px;
  left: 80%;
  width: 100%;
}
.ribbon {
  position: relative;
  font-size: 0.8rem;
  width: 20%;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #68a7ff;
  color: #fff;
  /* text-align: center; */
  z-index: 10;
}
.ribbon:before {
  content: "";
  position: absolute;
  display: block;
  left: -30px;
  border: 15px solid #68a7ff;
}
.ribbon:before {
  border-left-color: transparent;
}
@media only screen and (max-width: 1000px) {
  .College {
    flex-basis: 60%;
  }
}
@media only screen and (max-width: 992px) {
  .College {
    flex-basis: 70%;
  }
}
@media only screen and (max-width: 600px) {
  .College {
    flex-basis: 80%;
  }
  .rating-wrapper {
    background-color: #f5a626;
    position: absolute;
    top: 3.5vh;
    left: 66%;
    z-index: 10;
    width: 80px;
    /* padding: 0px 10px; */
    color: white;
    padding: 5px 10px;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .rating {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .rating-number {
    font-size: 1rem;
  }

  .rating-max {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 500px) {
  .College {
    flex-basis: 100%;
  }
  .college-tags-wrapper {
    flex-direction: column;
    height: 100px;
    top: 10%;
    left: -25%;
  }
  .college-tags-left {
    flex-direction: column;
    height: 50%;
  }
  .ribbon {
    font-size: 0.6rem;
  }
}
